            <div class="row six-dimensions" v-for="threeItems in aboutDetails" :key="threeItems[0][0]">
              <div class="col-md-4 col-sm-4 col-xs-12" v-for="item in threeItems" :key="item[0]">
                <div class="dimensions-item" style="padding-bottom:10px">
                  <div>
                    <img width="300" :src="item[3]" :alt="item[3]"  class="img-rounded">
                  </div>
                  <div>
                    <p class="dimensions-text" style="margin:0 auto; width:90%">{{ item[0] }}</p>
                    <p class="dimensions-text" style="margin:0 auto 15px auto; width:90%">{{ item[1] }}</p>
                    <p class="text-faded dimensions-text-sub" style="margin:0 auto; width:70%">{{ item[2] }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section id="preview" style="padding: 20px 0">
      <div class="container corpus-preview" style="width: 68%">
        <div class="row samples-div">
          <div class="text-center" style="padding-top: 50px">
            <div class="preview-title">
              <span class="line"></span>
              <h2 class="section-heading"> <i class="fa fa-table"></i> Corpus Preview</h2>
              <span class="line"></span>
            </div>
            <button class="download-btn btn" @click="download()" style="background: #f6f4ff"><i class="fa fa-cloud-download" aria-hidden="true"></i> Download</button> 
            <hr class="preview-hr">
            <vue-good-table :columns="tasks" :rows="taskId" styleClass="vgt-table bordered goodtable" style="margin-bottom: 50px">
            </vue-good-table>
            <vue-good-table :columns="columns" :rows="rows" styleClass="vgt-table bordered goodtable">
            </vue-good-table>
            <div class="tips">
              <h4>Tips:</h4>
              <p style="margin-bottom: 0;">task_id: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;task id of the sentence(1-10) </p>
              <p style="margin-bottom: 0;">exp_sen_id: expert sentence id of the sentence(1-920)</p>
              <p style="margin-bottom: 0;">is_exp_sen: whether the sentence is the expert sentence(0: no, 1: yes)</p>
              <p style="display: inline;">table_id: table id of the sentence(1-36)</p>
              <button class="download-table-btn btn" @click="downloadTables()" style="background: #f6f4ff"><i class="fa fa-cloud-download" aria-hidden="true" style="display: inline;"></i> Download Tables</button> 
              <p style="margin-bottom: 0;">expert_id: expert id of the sentence(1-20)</p>
            </div>
            <!-- <h3><i class="fa fa-table"></i> The analysis of 10 tasks</h3> -->
