            <p style="margin-bottom: 35px; margin-top: 15px; font-size: 17px" class="nl4dv-text">As a result, NL4DV-Quda is more robust in task classification and extends NL4DV to a broader range of usage scenarios.</p>
            <a class="btn btn-primary github-button" style="float:left; margin-left:17%" data-style="zoom-out" href="https://github.com/freenli/nl4dv-quda" target="_blank">
