<script>
import $ from 'jquery'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'
import 'bootstrap/dist/js/bootstrap.js'
import '../css/animate.min.css'
import 'font-awesome/css/font-awesome.min.css'
import 'vue-good-table/dist/vue-good-table.css'
import { VueGoodTable } from 'vue-good-table'


export default {
  name: 'HomePage',
  components: {
  VueGoodTable,
},
  data() {
    return {
      title: 'QUDA',
      subtitle: 'Natural Language Queries for Visual Data Analytics',
      projectDesc: 'In the context of visualization-oriented natural language interfaces, Quda contains 14,035 diverse user queries annotated with 10 low-level analytic tasks that assist in the deployment of state-of-the-art machine/deep learning techniques for parsing complex human language.',
      projectSubDesc: 'Quda contains 14,035 diverse user queries annotated with 10 low-level analytic tasks that assist in the deployment of state-of-the-art techniques for parsing complex human language.',
      aboutDetails: [
        [["Abstraction:", "Low", "The abstraction level describes how concrete a query is.", "img/icons/abstraction.svg"], ["Composition:", "Low", "The composition level describes to what extent a query encompasses sub-queries.", "img/icons/composition.svg"], ["Perspective:", "Objectives", "Instead of enumerating actions, we aim to collect queries that are objectives raised by data analysts.","img/icons/perspective.svg"]],
        [["Type of Data:", "Table", "Data and dataset can be categorized into five types, i.e., table, networks & trees, fields, geometry, and clusters & sets & lists.","img/icons/table.svg"], ["Type of Tasks:", "10 Low-level Tasks", " Retrieve Value, Correlate, Compute Derived Value, Find Anomalies, etc.","img/icons/tasks.svg"], ["Context Dependency:", "Independent", "Focus on queries that have complete references to tasks or values associated with a task.","img/icons/context.svg"]],
      ],
      license: 'It is released for non-commercial use under the CC BY-NC-SA 3.0 license.',
      contactDesc: "Questions? Comments?",
      acknowledgement: "This work has been supported in part by the National Science Foundation (NSF) under grant 1016862, MIT Big Data Initiative at CSAIL, Google, and Xerox awards to Aude Oliva. This work has also been made possible through support from the Department of Defense through the National Defense Science & Engineering Graduate Fellowship (NDSEG) Program, the NSF Graduate Research Fellowship Program, the Natural Sciences and Engineering Research Council of Canada Postgraduate Doctoral Scholarship (NSERC PGS-D), and the Kwanjeong Educational Foundation.",
      tasks: [
        {
          label: 'Retrieve Value',
          field: 'Retrieve Value',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Filter',
          field: 'Filter',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Compute Derived Value',
          field: 'Compute Derived Value',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Find Extremum',
          field: 'Find Extremum',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Sort',
          field: 'Sort',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Determine Range',
          field: 'Determine Range',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Characterize Distribution',
          field: 'Characterize Distribution',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Find Anomalies',
          field: 'Find Anomalies',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Cluster',
          field: 'Cluster',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
        {
          label: 'Correlate',
          field: 'Correlate',
          sortable: false,
          type: 'number',
          tdClass: 'table-text-center'
        },
      ],
      taskId: [
        {  
          'Retrieve Value': 1,
          'Filter': 2,
          'Compute Derived Value': 3,
          'Find Extremum': 4, 
          'Sort': 5,
          'Determine Range': 6,
          'Characterize Distribution': 7,
          'Find Anomalies': 8,
          'Cluster': 9,
          'Correlate': 10
        }
      ],
      columns: [
        {
          label: 'task_id',
          field: 'task_id',
          type: 'number',
          tdClass: 'table-text-center',
        },
        {
          label: 'exp_sen_id',
          field: 'exp_sen_id',
          type: 'number',
          tdClass: 'table-text-center',
        },
        {
          label: 'is_exp_sen',
          field: 'is_exp_sen',
          type: 'number',
          tdClass: 'table-text-center',
        },
        {
          label: 'table_id',
          field: 'table_id',
          type: 'number',
          tdClass: 'table-text-center',
        },
        {
          label: 'expert_id',
          field: 'expert_id',
          type: 'number',
          tdClass: 'table-text-center',
        },
        {
          label: 'sentence',
          field: 'sentence',
          type: 'text',
        }
      ],
      rows: [
        { id:1, task_id:"1", exp_sen_id: 1, is_exp_sen: 0, table_id: 1, expert_id: 1, sentence: 'show me arkansas\' population on july 1 , 2002 .'},
        { id:2, task_id:"1", exp_sen_id: 715, is_exp_sen: 1, table_id: 35, expert_id: 16, sentence: 'how many residents in orange county are asians ?'},
        { id:3, task_id:"2", exp_sen_id: 90, is_exp_sen: 0, table_id: 11, expert_id: 3, sentence: 'can you tell me the team that won the game ?'},
        { id:4, task_id:"2", exp_sen_id: 91, is_exp_sen: 1, table_id: 7, expert_id: 3, sentence: 'which country has more females than males ?'},
        { id:5, task_id:"3,2", exp_sen_id: 105, is_exp_sen: 0, table_id: 3, expert_id: 4, sentence: 'in western europe , what is the average gdp ?'},
        { id:6, task_id:"3,2", exp_sen_id: 171, is_exp_sen: 1, table_id: 13, expert_id: 5, sentence: 'on average , how long are the books published by dark horse comics ?'},
        { id:7, task_id:"4", exp_sen_id: 246, is_exp_sen: 0, table_id: 15, expert_id: 6, sentence: 'i would like to have the highest rating number .'},
        { id:8, task_id:"4", exp_sen_id: 262, is_exp_sen: 1, table_id: 1, expert_id: 1, sentence: 'what is the longest study time that students have ?'},
        { id:9, task_id:"5,2", exp_sen_id: 358, is_exp_sen: 1, table_id: 15, expert_id: 7, sentence: 'list all the expensive apps , from the most recent to the oldest .'},
        { id:10, task_id:"5", exp_sen_id: 378, is_exp_sen: 0, table_id: 22, expert_id: 8, sentence: 'list all the movies according to their release year .'},
        { id:11, task_id:"6", exp_sen_id: 433, is_exp_sen: 1, table_id: 24, expert_id: 9, sentence: 'what is the range of the number of undergraduate students ?'},
        { id:12, task_id:"6,2", exp_sen_id: 491, is_exp_sen: 0, table_id: 26, expert_id: 10, sentence: 'in the south american countries , how large is the range of registered soccer players ?'},
        { id:13, task_id:"7,2", exp_sen_id: 770, is_exp_sen: 0, table_id: 36, expert_id: 17, sentence: 'determine the value distribution of deaf -blind patients .'},
        { id:14, task_id:"7,2", exp_sen_id: 810, is_exp_sen: 1, table_id: 32, expert_id: 18, sentence: 'show me the distribution of the population ratio binned with a bin size of 0.5 .'},
        { id:15, task_id:"8", exp_sen_id: 236, is_exp_sen: 1, table_id: 15, expert_id: 6, sentence: 'is there any app that is significantly different from the others ?'},
        { id:16, task_id:"8,1", exp_sen_id: 441, is_exp_sen: 0, table_id: 24, expert_id: 9, sentence: 'identify the anomalous values of both postgraduate and undergraduate income .'},
        { id:17, task_id:"9", exp_sen_id: 916, is_exp_sen: 0, table_id: 32, expert_id: 20, sentence: 'group the length of trip by initial sites .'},
        { id:18, task_id:"9,4", exp_sen_id: 532, is_exp_sen: 1, table_id: 27, expert_id: 11, sentence: 'find the cluster with the highest number of births among the neighborhoods across the years .'},
        { id:19, task_id:"10", exp_sen_id: 80, is_exp_sen: 1, table_id: 3, expert_id: 2, sentence: 'can we infer that higher freedom leads to higher happiness ?'},
        { id:20, task_id:"10", exp_sen_id: 613, is_exp_sen: 0, table_id: 30, expert_id: 13, sentence: 'are the ages of the actors and their winning results correlated ?'},
      ],
      freenli: 'We explore the use of Quda in a visualization-oriented natural language interface, called FreeNLI. The features of FreeNLI have two aspects: (1) It accepts free-form natural language as input, and (2) forms effective design decision by recognizing analytic tasks from the input query.',
      placeholder: "This is the first sentence.\nThis is the second sentence.",
      task_map: {
        '0': 'Others',
        '1': 'Retrieve Value',
        '2': 'Filter',
        '3': 'Compute Derived Value',
        '4': 'Find Extremum',
        '5': 'Sort',
        '6': 'Determine Range',
        '7': 'Characterize Distribution',
        '8': 'Find Anomalies',
        '9': 'Cluster',
        '10': 'Correlate'
      }
    }
  },
  methods: {
    smoothScroll: function (event) {
      var anchor = $(event.target)
      // $("html, body").animate({scrollTop: $(anchor.attr("href")).offset().top -20+ "px"}, 500)
      $('html, body').stop().animate({scrollTop: $(anchor.attr('href')).offset().top - 50}, 1250)
      event.preventDefault()
    },
    validateEmail: function (email) {
      var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
      return re.test(email);
    },
    downloadDataset: function () {
      if ($("#licensechk").prop('checked') == false) {
        $("#license-alert").show()
        setTimeout(function () {
          $("#license-alert").hide(400)
        }, 2000)
        return
      }
      //validate from data
      //console.log(this.name + "," + this.email + "," + this.inst+ "," + this.instAddr+ "," + this.ivgrName + "," + this.ivgrEmail + "," + this.othrEmail+ "," + this.rqstResn)
      if (this.name == "" || this.email == "" || !this.validateEmail(this.email)) {
        console.log("not enough information")
        $("#request-alert").show()
        setTimeout(function () {
          $("#request-alert").hide(400)
        }, 2000)
        return
      }
      //request data
      var requested = []
      this.datasets.forEach(function (d) {
        if ($("#" + d.name).prop('checked')) {
          if (d.name == "all5k") {
            requested.push("news")
            requested.push("science")
            requested.push("government")
            requested.push("vis1")
            requested.push("vis2")
            requested.push("vis3")
          } else {
            requested.push(d.name)
          }
        }
      })
      if (requested.length == 0) {
        $("#data-alert").show()
        setTimeout(function () {
          $("#data-alert").hide(400)
        }, 2000)
        return
      }
      // var request = {
      //   name: this.name,
      //   email: this.email,
      //   inst: this.inst,
      //   inst_addr: this.instAddr,
      //   ivgr_name: this.ivgrName,
      //   ivgr_email: this.ivgrEmail,
      //   othr_email: this.othrEmail,
      //   rqst_resn: this.rqstResn,
      //   requested: requested
      // }
    },
    download: function () {
      //window.open("https://drive.google.com/file/d/1AUTbV4mV1YHXZ0g1tPU3lb6JJypqtc3s/view?usp=sharing")
      window.open("https://github.com/freenli/quda_corpus")
    },
    downloadTables: function () {
      window.open("https://github.com/freenli/quda-tables")
    }, 
    classification: function () {
      let that = this
      try {
        let list = {'sentences': JSON.stringify($("#sentences").val().split("\n"))}
        $.ajax({
          type: "POST",
          //url: "https://nl4dv-quda.projects.zjvis.org/get_query_tasks",
          url: "https://freenli.projects.zjvis.org/get_sentences_tasks/",
          data: list,
          success: function(result) {
            if (result.tasks) {
              result.tasks = JSON.parse(result.tasks)
              let taskResult = []
              let taskArray = []
              result.tasks.forEach(item => {
                taskArray = []
                item.forEach(element => {
                  taskArray.push(that.task_map[element])
                })
                taskResult.push(taskArray)
              })
              $("#result1").text(JSON.stringify(taskResult))
            }
            else {
              $("#result1").text(result.tasks)
            }
          },
          error: function(e) {
            console.log(e.status)
          }
        })
      }
      catch (e) {
        alert(e.message)
      }
    }
  },
  mounted() {
    $("#mainNav").affix({
      offset: {
        top: 100
      }
    })
    $.post('https://freenli.projects.zjvis.org/access_log/', {"ip":returnCitySN.cip,"id":returnCitySN.cid,"location":returnCitySN.cname, "type": 'homepage' }, function(result){
      if (!result.result){
          console.log(result.error);
      }
     })
    }
}
</script>


